import Currency from "../helper/Currency";

const RetailItem = ({ item }) => {

    return (
        <>
            <td><img src={item.image}
                className=" img-circle img-size-50 mr-2" alt="....."  />{item.id}
            </td>
            <th>{item.group}</th>
            <th>{item.barcode}</th>
            <th>{item.item}</th>
            <th>{Currency(item.item_price)}</th>
            <th>{Currency(item.itemp)}</th>
            <th>{item.quantity}</th>
            <th>{item.stock}</th>
            <th>{Currency(item.price)}</th>
            <th>{Currency(item.stockprice)}</th>
            <th>{Currency(item.qazanc)}</th>
            <th>{item.date}</th>
        </>
    )
}

export default RetailItem
