import React, {useContext, useRef, useState} from 'react'
import {Button, Form} from 'react-bootstrap';
import Select from 'react-select';
import { GroupsContext } from '../../contexts/GroupsContext';
import { TradersContext } from '../../contexts/TradersContext';

const AddCompanyForm = () => {
    const {addCompany} = useContext(TradersContext);
    const { groups } = useContext(GroupsContext)
    const [group, setGroup] = useState('')

    const groupsOpt = [...groups.map((opt) => ({ value: opt.id, label: opt.name }))]

    const nameRef = useRef('')
    const ownerRef = useRef('')
    const codeRef = useRef('')
    const phoneRef = useRef('')
    const addressRef = useRef('')

    const handleSubmit = (e) => {
        e.preventDefault();
        addCompany(
            {
                "name": nameRef.current,
                "code": codeRef.current,
                "exchange": ownerRef.current,
                "group": group,
                "image": null,
                "status": false,
                "address":addressRef.current,
                "phone": phoneRef.current,
            }
        )
    }

    return (
        <Form onSubmit={handleSubmit}>
             <Form.Group>
                <Form.Label ></Form.Label>
                <Select placeholder="..." name="group" options={groupsOpt}
                    onChange={(e) => setGroup(e.value)} />
            </Form.Group>
            <Form.Group>
                <Form.Label ></Form.Label>
                <Form.Control type='text'  ref={nameRef} onChange={(event)=>nameRef.current=event.target.value}/>
            </Form.Group>
            <Form.Group>
                <Form.Label ></Form.Label>
                <Form.Control type=''  ref={ownerRef} onChange={(event)=>ownerRef.current=event.target.value}/>
            </Form.Group>
            <Form.Group>
                <Form.Label ></Form.Label>
                <Form.Control type='text'  ref={codeRef} onChange={(event)=>codeRef.current=event.target.value}/>
            </Form.Group>
            <Form.Group>
                <Form.Label >.</Form.Label>
                <Form.Control type='text'  ref={phoneRef} onChange={(event)=>phoneRef.current=event.target.value}/>
            </Form.Group>
            <Form.Group>
                <Form.Label ></Form.Label>
                <Form.Control type='text'  ref={addressRef} onChange={(event)=>addressRef.current=event.target.value}/>
            </Form.Group>
            <hr/>
            <Button variant="success" type="submit">
                 
            </Button>
        </Form>
    )
}

export default AddCompanyForm
