import { Button, Form } from "react-bootstrap";
import React, { useContext, useState } from "react";
import { ReSellContext } from "../../contexts/ReSellContext";
import Select from "react-select";
import { ItemsContext } from "../../contexts/ItemsContext";


const ReSaleEditForm = ({ theReSale }) => {
    const { UpdateReSale } = useContext(ReSellContext);
    const { items } = useContext(ItemsContext)
    const itemsOpt = [...items.map((opt) => ({ value: opt.id, label: `${opt.name} ${opt.barcode}`, price: opt.price, quantity: opt.quantity }))]

    const [price, setPrice] = useState(theReSale.price)
    const [quantity, setQuantity] = useState(theReSale.quantity)
    const [item, setItem] = useState(theReSale.item)

    const handleSubmit = (e) => {
        e.preventDefault();
        UpdateReSale(theReSale.id, item, quantity, price);
    }

    const handleItem = (e) => {
        setItem(e.value)
    }


    return (
        <Form onSubmit={handleSubmit}>
            <Form.Group>
                <Form.Label ></Form.Label>
                <Form.Control type='number' defaultValue={item} value={item} onChange={(event) => setItem(event.target.value)} />
                <Select placeholder="..." name="item" options={itemsOpt}
                    onChange={(e) => handleItem(e)} />
            </Form.Group>
            <Form.Group>
                <Form.Label></Form.Label>
                <Form.Control type='number' defaultValue={quantity} value={quantity} onChange={(event) => setQuantity(event.target.value)} />
            </Form.Group>
            <Form.Group>
                <Form.Label></Form.Label>
                <Form.Control type='number' defaultValue={price} value={price} onChange={(event) => setPrice(event.target.value)} />
            </Form.Group>
            <Form.Group>
                <Form.Label></Form.Label>
                <Form.Control type={"number"} defaultValue={price * quantity} value={price * quantity} disabled={true} />
            </Form.Group>
            <hr />
            <Button variant="success" type="submit">
                  
            </Button>
        </Form>
    )
}

export default ReSaleEditForm