import React, { useContext, useEffect, useState } from 'react'
import { GroupsContext } from '../contexts/GroupsContext'
import { ItemsContext } from '../contexts/ItemsContext'
import Currency from '../helper/Currency'

const TopList = ({ list }) => {
    const { gropId  } = useContext(GroupsContext)

    
    const { items } = useContext(ItemsContext)
    const [tday, setTday] = useState([]);

    const fetchData = (startDate, endDate, group) => {
        const _item_sell = []
        // saleDetail.filter((i) => i.group === group)
        // updateSaleDate({ startDate, endDate }, group)
        // console.log(group);
        // items
        items.filter((i) => i.group === group)?.map((_item) => {
            let item_sell = _item.item_sell?.filter((sale) => (new Date(sale.date) - startDate) >= 0 && (new Date(sale.date) - endDate <= 0)) || []
            let quantity = Object.values(item_sell).reduce((r, { quantity }) => r + quantity, 0)
            let date = Object.values(item_sell)?.reduce((r, { date }) => date, 0) || null
            let price = Object.values(item_sell)?.reduce((r, { price, quantity }) => r + (parseFloat(quantity) * parseFloat(price)), 0) || null
            return _item_sell?.push({ "item": _item.name, "image": _item.image, "item_price": parseFloat(_item.price), "qazanc": price - (_item.price * quantity), "barcode": _item.barcode, "quantity": quantity, "stock": _item.stock, "stockprice": (_item.stock * _item.price), "price": price, "date": date, "group": _item.group, 'itemp': (price / quantity) })
        })
        setTday(_item_sell?.filter((_items) => _items.quantity !== 0))
    }

    useEffect(() => {
        var date = new Date();
        var firstDay = new Date(date.getFullYear(), date.getMonth() - 1, 1);
        var lastDay = new Date(date.getFullYear(), date.getMonth(), 0);
        fetchData(firstDay, lastDay, gropId);
        // eslint-disable-next-line
    }, [items, gropId])

    
    return (
        <div id="product">
            <table className="table table-striped table-valign-middle">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {tday
                        .filter((i) => i.deleted !== false)
                        .sort((a, b) => (a.qazanc) < (b.qazanc) ? 1 : -1)
                        .slice(0, 5)
                        .map((item, index) => (
                            <tr key={index}>
                                <td>
                                    <img src={item.image} alt="Product 1" className="img-circle img-size-50 mr-2" />
                                    {item.item}{item.quantity}
                                </td><td>{Currency(parseFloat(item.item_price))} </td><td>
                                    <small className="text-success mr-1">
                                        <i className="fas fa-arrow-up" />
                                        12%
                                    </small>
                                    {Currency(parseFloat(item.qazanc))}
                                </td><td>
                                    <p className="text-muted">
                                        <i className="fas fa-search" />
                                    </p>
                                </td>
                            </tr>
                        ))}
                </tbody>
            </table>
        </div>
    )
}

export default TopList