import React, { useContext } from 'react'
import { createContext } from 'react'
import { APIContext } from './APIContext'

export const ReSellContext = createContext(undefined)

const ReSellContextProvider = (props) => {
    const { XenderAIOS, reSellURL } = useContext(APIContext)

    const addReSale = (sell, item, quantity, price) => {
        XenderAIOS.post(reSellURL, {
            sell: sell, item: item, quantity: quantity, price: price
        }).then(() => {
            alert(" ");
        }).catch(err => {
            alert(" ");
        })
    }

    // UpdateReSale
    const UpdateReSale = (id, item, quantity, price) => {
        XenderAIOS.patch(`${reSellURL}${id}/`, {
            item: item, quantity: quantity, price: price
        }).then(() => {
            alert(" ");
        }).catch(err => {
            alert(" ");
        })
    }

    const value = { addReSale, UpdateReSale }

    return (
        <ReSellContext.Provider value={value}>
            {props.children}
        </ReSellContext.Provider>
    )
}

export default ReSellContextProvider
