import { faEdit } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import moment from 'moment';
import React, { useEffect, useState } from 'react'
import { Modal, OverlayTrigger, Tooltip, Button } from 'react-bootstrap';
import Currency from '../../helper/Currency';
import ReSaleEditForm from './ReSaleEditForm';

const LocalResell = ({ resell }) => {
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    useEffect(() => {
        handleClose()
    }, [resell])
    return (
        <>
            <td>{resell.sell}</td>
            <td>{resell.group_name}</td>
            <td>{resell.local_name}</td>
            <td>{resell.item_name}</td>
            <td>{resell.item_barcode}</td>
            <td>{resell.quantity}</td>
            <td>{Currency(parseFloat(resell.price))}</td>
            <td>{Currency(parseFloat(resell.price * resell.quantity))}</td>
            <td>{moment(new Date(resell.date)).format("DD/MM/YYYY")}</td>

            <OverlayTrigger
                overlay={
                    <Tooltip id={`tooltip-top`}>
                        
                    </Tooltip>
                }>
                <Button variant={"outline-danger"} onClick={handleShow} data-toggle="modal">
                    <FontAwesomeIcon icon={faEdit} /></Button>
            </OverlayTrigger>

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>
                         
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <ReSaleEditForm theReSale={resell} />
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}

export default LocalResell
